export default {
	data(){
		return{ 
			list:[]
		}; 
	},
	onLoad:function(){
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				//console.log(res);
				this.list=res.data
				//console.log(this.list);
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e) {
			//console.log(e);
			var newsid=e.currentTarget.dataset.newsid;
			//console.log(newsid);
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	},
}
